import { Typography } from "@mui/material";

export const Info = () => {
  return (
    <>
      <>
        <Typography
          variant="h6"
          style={{ display: "inline-block", opacity: 0.7 }}
        >
          2fishx.
        </Typography>
        <Typography
          variant="h4"
          gutterBottom
          style={{ display: "inline-block" }}
        >
          Nie chce mi si liczy
        </Typography>
        <Typography
          variant="h6"
          style={{ display: "inline-block", opacity: 0.7 }}
        >
          .com
        </Typography>
      </>
      <Typography variant="subtitle1" gutterBottom>
        Wpisz w pole poniej kwoty donejtw, bitsw i iloc subw z odpowiedni
        kocwk (z - zotych, b - bitsw, s - subw) i wcinij enter lub guzik
        obok by obliczy liczb zotych jak trzeba doda do licznika w Tipply.
        Poniej jest te historia dodawanych kwot.
      </Typography>
      <Typography variant="body1">Przykady:</Typography>
      {["10.5z", "200b150b", "5s20s30z", "20z30z1000z42s700b69b"].map((e) => (
        <Typography key={e} variant="body1">
          {e}
        </Typography>
      ))}
    </>
  );
};
